import React, { useEffect } from 'react';
import { navigate } from '@reach/router';
import { MapComponent } from '../components/Map/index';

export const Home = ({ loggedIn }) => {
  useEffect(() => {
    if (!loggedIn) {
      navigate(`${process.env.PUBLIC_URL}/login`, { replace: true });
    }
  }, []);
  return <MapComponent />;
};
